@import ../../main.sass

.contact-container
    @include flex(100%,fit-content,column)
    align-items: center
    justify-content: center

    .title
        @include grid(80%,15vh)
        text-align: center
        font-size: 22px

    .info-wrapper
        @include flex(80%, fit-content, column)
        justify-content: center
        align-items: center
        
        background: black
        color: white
        padding: 10px 0 40px 0
        gap: 20px

    .element
        @include flex(30%,70px,column)
        min-width: fit-content
        justify-content: center

        align-items: flex-start
        font-size: 20px

        .label
            @include flex(100%,50%,row)

        img
            height: 25px
            margin-right: 20px
        .name
            margin-right: 10px
        .text
            width: 100%
            margin-left: 50px

@media screen and (max-width: 700px)
    .contact-container
        margin-top: 30px
        align-items: space-evenly
        .title
            font-size: 16px
            height: 10vh
            width: 80%
            text-align: justify

        .info-wrapper
            width: 95%
            min-height: 550px
            padding: 0px

        .element
            width: 60%
            font-size: 16px
            //.label

            img
                height: 18px
                margin-right: 10px
            .name
                margin-right: 10px
            .text
                margin-left: 30px