@import ../../main.sass

.shop-container
    @include flex(100%,fit-content,column)
    position: relative
    align-items: center

    .search-bar
        @include flex(100%,40px,row)
        justify-content: center
        align-items: center
        font-size: 20px

        .search, .filter
            @include flex(50%,fit-content,row)
            justify-content: center
            align-items: center

        .label
            margin: 0 10px

        input
            border: none
            background: #999999
            border-radius: 15px
            width: 40%
            height: 30px
            padding: 0 10px
            color: white
            font-size: 18px

            &:focus
                outline: none

        option
            background: white
            color: grey

        select
            border: none
            background: #999999
            border-radius: 15px
            width: 40%
            height: 30px
            padding: 0 5px
            color: white
            font-size: 18px
            &:focus
                outline: none
                border: none

        img
            height: 20px
            margin: 0 5px

    .items-wrapper
        @include flex(80%, fit-content, row)
        flex-wrap: wrap
        gap: 10px

        margin: 5% 0

@media screen and (max-width: 700px)
    .shop-container
        overflow: hidden
        .search-bar
            flex-direction: column
            height: 100px
            font-size: 16px

            .filter, .search
                width: 100%
                height: 50%
                justify-content: center
                align-items: center

            select, input
                font-size: 14px
        .items-wrapper
            justify-content: center
            gap: 20px
                