@import ../../main.sass

.info-card-container
    @include grid(100%, 100vh)
    position: fixed
    top: 0
    z-index: 3
    background: #0000005d
    
    .content
        @include flex(80%, 80%, row)
        position: relative
        min-width: 300px
        justify-content: space-between
        align-items: center

        background: white

        box-shadow: 0px 0px 5px #00000079

        .close-button
            position: absolute
            height: 20px
            top: 10px
            right: 10px
            background: white
            padding: 0

            &:hover
                cursor: pointer
                opacity: 0.8
        
        .gallery
            @include flex(50%,100%,column)
            justify-content: space-evenly
            align-items: center

            background: #bebebe

            .mainImg
                height: 400px
                width: 400px
                object-fit: scale-down
            
            .imgs-wrapper
                @include flex(100%,10%,row)
                justify-content: center
                align-items: center

                gap: 10px

                img
                    width: 60px
                    height: 60px
                    object-fit: scale-down
                    background: white
                    &:hover
                        cursor: pointer
                        opacity: 0.8

        .purchase
            @include flex(50%,90%,column)
            justify-content: space-evenly
            align-items: center

            .info
               @include flex(60%,45%,column)
               justify-content: space-evenly
               align-items: center
              
               font-size: 14px
            img
                width: 20px
                padding: 8px 20%
                border-radius: 20px
                background: #bebebe

                &:hover
                    cursor: pointer
                    opacity: 0.8

            .purchase-button
                @include grid(60%,40px)
                background: #bebebe
                font-size: 20px
                opacity: 0.7
             
                
                    

@media screen and (max-width: 700px)
    .info-card-container
        .content
            height: 90%
            width: 95%
            flex-direction: column

            .close-button
                background: #bebebe

            .gallery, .purchase
                width: 100%
                height: 55%

                .mainImg
                    height: 210px
        
            .purchase
                height: 45%
                justify-content: space-evenly
                .info
                    width: 80%
                    font-size: 12px
                .purchase-button
