@import ../../main.sass
.podcast-container
    @include flex(70%, 450px, column)
    position: relative
    overflow: hidden
    align-items: center
    justify-content: space-evenly

    background: rgb(252,166,46)

    iframe
        height: 335px
        width: 600px

    .title
        font-size: 19px
        font-weight: bold

.first
    &::before
        position: absolute
        content: 'Nowy odcinek!'
        right: -40px
        top: 40px
        width: 200px
        height: 30px
        background: $packRed
        text-align: center
        transform: rotate(45deg)
        box-shadow: 0px 0px 4px #0000008c
        color: white

@media screen and (max-width: 700px)
    .podcast-container
        width: 95%
        height: 300px
        justify-content: space-evenly

        .title
            font-size: 12px
            width: 80%
            text-align: center
        iframe
            height: 200px
            width: 300px