@import ../../main.sass

.intro-container
    @include flex(100%, 100vh, row)
    position: absolute
    overflow: hidden
    z-index: 5
    font-family: 'Arimo', sans-serif

    justify-content: center
    align-items: center
    background: white

    .hint
        position: absolute
        opacity: 0
        animation: 1s show ease-in forwards
        top: 20%

    .before
        &::before
            left: 100% 
    .after
        &::before
            left: 0%

    .text
        @include grid(30%,55px)
        position: relative

        color: black
        font-size: 40px

        //overflow: hidden
        z-index: 1

        &::before
            position: absolute
            content: ''
            width: 100%
            height: 100%
            background: white
            transition: 1.5s
            z-index: 2

    img
        width: 20%
        object-fit: scale-down

        filter: drop-shadow(0px 0px 10px white)
        transition: 0.5s

        &:hover
            cursor: pointer


@keyframes show
    0%
        opacity: 0
    100%
        opacity: 1

@media screen and (max-width: 700px)
    .intro-container
        .text
            font-size: 14px
        