@import ../../main.sass

.card-container
    @include flex(80%,400px,row)
    position: relative
    overflow: hidden

    box-shadow: 0px 0px 5px #000000
    background: linear-gradient(to right,#000 50%,#474747)

    img
        height: 100%
        width: 35%
        max-width: 400px
        object-fit: cover

    .content
        @include flex(75%,100%,column)
        justify-content: center
        align-items: center

        .title
            @include grid(60%,10%)
            position: relative

            background: white
            color: black
            font-size: 25px
            z-index: 2
            box-shadow: 0px 0px 4px #000000

            &::before, &::after
                position: absolute
                box-shadow: 0px 0px 4px #000000
                content: ''
                width: 5%
                height: 100%
                background: white
                top: 10%
                z-index: 1

            &::before
                left: -5%
                
            &::after
                right: -5%

        .specyfic
            @include grid(65%,80px)
            text-align: center
            color: white
            font-size: 18px

        .description
            @include grid(60%,fit-content)
            position: relative
            padding: 20px 0
            font-size: 15px
            text-align: justify
            color: white

            &::before, &::after
                position: absolute
                content: ''
                width: 60%
                height: 2px
                background: $packRed
                left: 20%
            &::before
                top: 0
                
            &::after
                bottom: 0

@media screen and (max-width: 700px)
    .card-container
        flex-direction: column
        height: 600px

        img
            height: 45%
            width: 100%

        .content
            height: 55%
            width: 100%

            .title
                width: 70%
                font-size: 22px
            .specyfic
                width: 75%
                font-size: 13px
            .description
                width: 70%
                font-size: 12px

