@import '../../main.sass'

.banner-container
    @include flex(98%, 300px,column)
    box-sizing: border-box
    font-size: 25px

    margin: 20px 0

    &:hover
        cursor: pointer

    img
        height: 100%
        object-fit: scale-down

@media screen and (max-width: 700px)
    .banner-container
        height: 300px 
        img
            height: 300px
            object-fit: cover
