@import ../../main.sass

.home-container
    @include flex(100%,fit-content,column)
    position: relative
    background: white

    .background-img
        width: 100%
        position: fixed
        top: 15vh
        object-fit: cover
        z-index: 1

    .background-wrapper
        @include flex(100%, 600px, column)
        position: relative
        z-index: 2
        justify-content: center

        .background-description
            @include flex(35%,80%,column)
            justify-content: center
            align-items: center
            position: absolute

            margin-left: 6% 

            background: rgba(255,255,255,0.2)
            box-shadow: 0px 0px 4px #000000c2

            color: white
            font-size: 55px
            font-weight: bold
            z-index: 2

            .h1
                padding: 10px 0

            .h2
                font-family: 'Open Sans', sans-serif
                font-weight: 100
                width: 100%
                text-align: center
                font-size: 15px
                margin-top: 100px
        
    .content
        @include flex(100%, fit-content, column)
        align-items: center
        
        background: white
        z-index: 2

        .spotify
            width: 60%
            height: 380px
            border-radius: 10px
            margin: 20px 0

        .spotify-label
            font-size: 25px
        .spotify-description
            font-size: 16px
            width: 60%
            padding: 10px 0

        .spotify-stats
            @include flex(60%, 150px, row)
            justify-content: space-evenly
            align-items: center

            .stat
                @include flex(30%,fit-content, row-reverse)
                min-width: fit-content
                justify-content: center
                align-items: center

                .name
                    font-size: 12px
                    font-weight: bold
                    text-align: center
                .number
                    @include grid(60px, 60px)
                    margin-right: 20px
                    color: white
                    font-weight: bold
                    font-size: 14px
                    background: linear-gradient(0deg,black,$packRed)
                    border-radius: 50%

        .youtube-wrapper
            @include flex(100%, 700px,column)
            justify-content: space-evenly
            align-items: center

            margin: 10px 0 0 0
            background: white
            position: relative

            .label
                @include flex(20%, 30%, column)
                min-width: 300px
                min-height: 200px
                justify-content: space-evenly
                align-items: center

                background: white
                border-radius: 5px
                box-shadow: 0px 0px 5px #00000050
                z-index: 2

                img
                    position: relative
                    width: 70px
                    height: 70px
                    border-radius: 50%

                .title
                    font-size: 20px
                    font-weight: bold
                .stat
                    font-weight: 400
                    font-size: 15px
                    text-align: center

            img
                width: 100%
                height: 700px
                object-fit: cover
                position: absolute
                z-index: 1

            .youtube
                width: 660px
                height: 415px
                z-index: 2

        .general-info
            @include flex(100%, 550px, row)
            position: relative
            
            border-radius: 5px
            box-shadow: 0px 2px 10px 1px #530000a8

            img
                width: 60%
                height: 100%
                object-fit: cover

            .description
                @include flex(40%, 100%, column)
                left: 5%
                top: 5%
                justify-content: space-evenly
                align-items: center
                color: white

                background: linear-gradient(25deg, rgba(0,0,0,1) 0%, rgba(19,6,6,1) 34%, rgba(75,18,18,1) 63%, rgba(255,0,0,1) 100%)

                .title
                    font-size: 25px
                    font-weight: bold
                    
                .text
                    font-size: 15px
                    width: 80%
                    text-align: justify !important

                .button
                    background: $packRed
                    padding: 4px 8px
                    border-radius: 20px
                    color: white
                    font-size: 18px
                    margin: 20px 0

        .instagram-container
            @include flex(100%,fit-content,column)
            align-items: center
            padding-bottom: 5px

            .title
                @include flex(35%, 120px,row)
                justify-content: space-evenly
                align-items: center
                font-size: 25px
                img
                    height: 22px
                    &:hover
                        opacity: 0.8
                        cursor: pointer

        .instagram-wrapper
            @include flex(80%,fit-content,row)
            justify-content: center
            align-items: center
            flex-wrap: wrap
            gap: 10px

            img
                height: 20vw
                width: 20vw
                object-fit: scale-down

                box-shadow: 0px 0px 5px #00000083


@media screen and (max-width: 700px)
    .home-container
        width: 100%
        overflow-x: hidden 
        .background-img
            height: 600px
            top: 0
        
        .background-wrapper
            height: 500px
            justify-content: center
            align-items: center

            .background-description
                width: 90%
                margin: 0
                font-size: 25px

        .content
            .spotify
                width: 90%

            .spotify-label
                font-size: 25px
            .spotify-description
                font-size: 14px
                width: 80%
                padding: 10px 0

            .spotify-stats
                width: 100%
                height: fit-content
                align-items: center
                flex-wrap: wrap
                .stat
                    width: 70%
                    justify-content: flex-end
                    margin: 5px
                    .number
                        width: 60px
                        height: 60px

            .youtube-wrapper
                height: 450px
                img
                    height: 100%

                .youtube
                    width: 90%
                    height: 200px

                .label
                    margin: 20px 0
                

            .general-info
                flex-direction: column
                height: fit-content
                width: 97%

                img
                    width: 100%
                    height: 300px

                .description
                    width: 100%
                    height: fit-content
                    padding: 25px 0

                    .title
                        font-size: 15px

                    .text
                        font-size: 12px

            .instagram-container
                .title
                    font-size: 20px
                    width: 90%

            .instagram-wrapper
                width: 90%

                .title
                    height: 40px

                img, video
                    height: 100px
                    width: 100px
                    object-fit: scale-down

                    box-shadow: 0px 0px 5px #00000083