@import ../../main.sass


.footer-wrapper
    @include flex(100%, 200px, column)
    background: white
    z-index: 2
    justify-content: flex-end

.footer-container
    @include flex(100%, 100px, row)
    justify-content: space-between
    align-items: center

    box-shadow: 0px 0px 5px #00000057
    color: grey

.copyright
    @include flex(30%, 100%, row)
    justify-content: center
    align-items: center

    img
        height: 20px
        margin-right: 10px

    .h1
        font-size: 14px
        margin-right: 10px
    
.h2
    font-size: 10px
    margin-right: 30px

@media screen and (max-width: 700px)
    .footer-wrapper
        height: 120px

    .footer-container
        flex-direction: column
        height: 80px
        
    .copyright
        width: 100%
        height: 60%
        text-align: center

    .h2
        @include grid(100%,40%)
        margin: 0
        font-size: 8px