@import '../../main.sass'


.login-container
    @include grid(100%, 100vh)
    position: relative

    font-family: 'Merriweather', serif
    background: black

    overflow: hidden

    &::before
        position: absolute
        content: ''
        width: 70%
        height: 100%
        left: -30%
        background: linear-gradient(to left,#2c2c2c4d,#8886864d)
        transform: skew(45deg)
        z-index: 1


    .login-window
        @include flex(25%,55%, column)
        min-width: 300px
        align-items: center

        z-index: 2
        border: 2px solid white
        background: $packRed
        gap: 20px
        font-size: 18px

        .title
            @include grid(100%,20%)

            font-family: 'Open Sans', sans-serif
            font-size: 30px
            background: black
            color: white
        
        input
            width: 70%
            height: 8%

            border: none
            font-size: 18px

            &:focus
                outline: none

        .button
            @include grid(70%, 7%)
            border: 2px solid black
            background: black
            color: white
            margin-top: 5%

            &:hover
                cursor: pointer
                background: #0f0f0f