@import ../../main.sass

.shop-item-container
    @include flex(225px,250px,column)
    justify-content: space-between
    align-items: center

    box-shadow: 0px 0px 4px #00000054
    border-radius: 2px

    &:hover
        box-shadow: 0px 0px 4px #00000081

    img
        height: 50%
        margin-top: 5%
        object-fit: contain
        

    .info
        @include flex(100%, 40%, column)
        background: #dfdede
        justify-content: space-evenly
        align-items: center

        .name
            font-size: 18px
        .price
            font-size: 16px

        .button
            @include grid(30%,30px)
            justify-self: flex-end
            border-radius: 2px
            color: white
            background: black

            &:hover
                cursor: pointer
                background: #222222

@media screen and (max-width: 700px)
    .shop-item-container
        @include flex(325px,350px,column)

