@import '../../main'

.dashboard-container
    @include flex(100%, 100vh, row)

    .content
        @include grid(80%, 100%)
        font-size: 20px
        color: black


    .side-menu
        @include flex(20%, fit-content, column)
        align-items: center

        .menu-bar 
            @include grid(90%, 40px)
            font-size: 18px
            color: black
            margin: 2px 0
            box-sizing: border-box
            border: 2px solid black
            text-decoration: none

            &:hover
                cursor: pointer
                background: #bdbdbd