@import ../../main.sass

.podcasts-container
    @include flex(100%,fit-content, column)
    align-items: center
    
    gap: 10px

    .podcast-info
        @include flex(70%, 400px, column)
        align-items: center

        background: rgb(252,166,46)

        .title
            @include flex(25%, 80px, row)
            margin-top: 10px
            justify-content: space-between
            align-items: center
            font-weight: bold
            font-size: 20px
            img
                height: 30px

        .content
            @include flex(90%,100%,row)
            justify-content: space-evenly
            align-items: center

            .text
                @include flex(50%,fit-content, column)
                text-align: justify

                .button
                    width: 100%
                    margin: 20px 0
                    padding: 10px 0
                    background: white
                    text-align: center
                    cursor: pointer

                    &:hover
                        opacity: 0.8

            img
                width: 40%
                object-fit: scale-down

@media screen and (max-width: 700px)
    .podcasts-container
        margin-top: 50px
        .podcast-info
            width: 95%
            height: 500px
            .title
                width: 80%

            .content
                flex-direction: column

                .text
                    width: 95%
                    height: 60%
                    font-size: 14px