@import ../../main.sass


.desktop-menu-container
    @include flex(100%, 15vh, row)
    position: fixed
    top: 0

    align-items: center
    justify-content: space-between
    user-select: none

    background: white
    box-shadow: 0px 0px 5px #00000057
    z-index: 3

    img
        height: 60%
        margin: 0 5%

    .link-wrapper
        @include flex(50%, 100%, row)

        justify-content: space-evenly
        align-items: center

        .link
            position: relative
            color: black
            font-size: 17px
            text-decoration: none
            transition: 1s

            &::before
                position: absolute
                content: ''
                top: 90%
                width: 0%
                margin-left: 50%
                height: 2px
                background: black

            &:hover
                &::before
                    animation: 1s underline forwards

        .active
            &::before
                width: 100%
                margin-left: 0%

        .icon-link
            height: 18px
            margin: 0

        .link, .icon-link
            &:hover
                cursor: pointer
   
    
    
.mobile-menu-container
    @include flex(100%, fit-content, column)
    min-height: 50px
    position: fixed

    background: white
    color: black

    transition: 1s
    z-index: 3

    box-shadow: 0px 0px 5px #00000057

    .header
        @include flex(100%, 50px, row)
        justify-content: space-between
        align-items: center

    .menu
        @include flex(100%, fit-content, column)
        animation: 0.5s show forwards
        animation-delay: 0.5s
        opacity: 0
        align-items: center
    
    .hidden-menu
        display: none

    .button
        @include flex(10%, 100%, column)
        justify-content: center
        align-items: flex-end
        align-self: flex-end
        margin-right: 20px

    img
        height: 35px
        margin: 0 20px
        object-fit: scale-down

    .link
        @include grid(100%,100px)
        position: relative
        color: black
        font-size: 25px
        text-decoration: none

    .media-link-wrapper
        @include flex(40%,100px,row)
        justify-content: space-evenly
        align-items: center

        img
            height: 25px

@keyframes show
    0%
        opacity: 0
    100%
        opacity: 1

@keyframes underline
    0%
        width: 0%
        margin-left: 50%
    100%
        width: 100%
        margin-left: 0%

@media screen and (max-width: 700px)
    .desktop-menu-container
        display: none

@media screen and (max-width: 900px)
    .desktop-menu-container
        .link-wrapper
            width: 65%
            .link
                font-size: 15px

            .icon-link
                height: 15px

@media screen and (max-width: 1000px)
    .desktop-menu-container
        .link-wrapper
            width: 55%

@media screen and (min-width: 701px)
    .mobile-menu-container
        display: none